@import "https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

body {
  height: 100vh;
  margin: 0;
  padding: 0;
}

#root {
  place-items: center;
  width: 100%;
  height: 100%;
  font-family: Open Sans, sans-serif;
  transition: background .4s ease-out;
  display: grid;
}

#quote-box {
  text-align: center;
  background: #fff;
  border-radius: .5rem;
  max-width: 50rem;
  margin: 0;
  padding: 2rem;
  transition: all .3s ease-out;
  overflow: hidden;
}

#author {
  text-align: right;
}

.quote-quotes {
  vertical-align: bottom;
  width: 2.5rem;
  line-height: 0;
  display: inline-block;
}

.btn-row {
  justify-content: space-between;
  display: flex;
}

#tweet-quote, #new-quote {
  color: #fff;
  cursor: pointer;
  background: teal;
  border: none;
  border-radius: 5px;
  padding: .5rem;
  font-family: inherit;
  line-height: 0;
  transition: background .4s ease-out;
}

#new-quote {
  padding: .5rem 1rem;
}

.twitter-icon {
  width: 2rem;
  height: 2rem;
}

